import React from "react"
// import { Link } from "gatsby"

import Layout from "../components/layout";
// import Image from "../components/image"
import SEO from "../components/seo"
import { Carousel, Row, Container, Card, Col, Button } from "react-bootstrap";
// import 'holderjs';

import ImageOne from '../images/1.jpg';
import ImageTwo from '../images/2.jpg';
import ImageThree from '../images/3.jpg';



const IndexPage = () => {

    return(
        <Layout>
            <SEO title="Home" />
            <div className="jumbotron">
                <div className="container">
                    <h1 className="display-5">Welcome!</h1>

                    <Carousel id='home-carousel'>
                        <Carousel.Item>
                            <img className="d-block w-100"
                                alt="First slide"
                                src={ImageOne}
                            />

                            <Carousel.Caption>
                                <h3>Beautiful countryside</h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src={ImageTwo}
                                alt="Second slide"
                            />

                            <Carousel.Caption>
                                <h3>Contemporary accommodation ideally located</h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src={ImageThree}
                                alt="Third slide"
                            />

                            <Carousel.Caption>
                                <h3>Other thing</h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                    </Carousel>
                    <br/>
                    <p className='lead'>This should be a short description selling what 'Rhylla Devon' is.</p>
                    <p className="lead">
          Situated in the Tamar Valley, Rhylla Cottage offers wonderful views of the valley and river below and offers a perfect stay for people who enjoy peace, tranquillity and cosy home from home comforts.
                    </p>
                </div>
            </div>

            <Container fluid>
                <Row>
                    <Col style={{marginBottom: 24}}>
                        <Card>
                            <Card.Body>
                                <Card.Title>Our accommodation</Card.Title>
                                <Card.Text>
                Rhylla Cottage is ideally situated for those wanting an abundance of local walks, cycling trails and other activities.
                                </Card.Text>
                                <Button variant="primary" size='lg'>See what's available</Button>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col style={{marginBottom: 24}}>
                        <Card>
                            <Card.Body>
                                <Card.Title>Interested in staying or have a question?</Card.Title>
                                <Card.Text>
                Get in contact with us and we'll respond as soon as possible!
                                </Card.Text>
                                <Button variant="primary" size='lg'>Contact Us</Button>
                            </Card.Body>
                        </Card>
                    </Col>
                    {/* <Col>
          <Card style={{  }}>
            <Card.Body>
              <Card.Title>Card Title</Card.Title>
              <Card.Text>
                Some quick example text to build on the card title and make up the bulk of
                the card's content.
              </Card.Text>
              <Button variant="primary">Go somewhere</Button>
            </Card.Body>
          </Card>
        </Col> */}
                </Row>
            </Container>
    
   
        </Layout>
    )
};

export default IndexPage
